@keyframes shake {
  0% { transform: translate(2px, 2px) rotate(0deg); }
  3% { transform: translate(-1px, -2px) rotate(-1deg); }
  6% { transform: translate(-3px, 0px) rotate(1deg); }
  9% { transform: translate(3px, 2px) rotate(0deg); }
  12% { transform: translate(1px, -1px) rotate(1deg); }
  15% { transform: translate(-1px, 2px) rotate(-1deg); }
  18% { transform: translate(-3px, 1px) rotate(0deg); }
  21% { transform: translate(3px, 1px) rotate(-1deg); }
  24% { transform: translate(-1px, -1px) rotate(1deg); }
  27% { transform: translate(1px, 2px) rotate(0deg); }
  30% { transform: translate(1px, -2px) rotate(-1deg); }
  33% { transform: translate(2px, 2px) rotate(0deg); }
  36% { transform: translate(-1px, -2px) rotate(-1deg); }
  39% { transform: translate(-3px, 0px) rotate(1deg); }
  42% { transform: translate(3px, 2px) rotate(0deg); }
  45% { transform: translate(1px, -1px) rotate(1deg); }
  48% { transform: translate(-1px, 2px) rotate(-1deg); }
  51% { transform: translate(-3px, 1px) rotate(0deg); }
  54% { transform: translate(3px, 1px) rotate(-1deg); }
  57% { transform: translate(-1px, -1px) rotate(1deg); }
  60% { transform: translate(1px, 2px) rotate(0deg); }
  63% { transform: translate(1px, -2px) rotate(-1deg); }
  66% { transform: translate(2px, 2px) rotate(0deg); }
  69% { transform: translate(-1px, -2px) rotate(-1deg); }
  72% { transform: translate(-3px, 0px) rotate(1deg); }
  75% { transform: translate(3px, 2px) rotate(0deg); }
  78% { transform: translate(1px, -1px) rotate(1deg); }
  81% { transform: translate(-1px, 2px) rotate(-1deg); }
  84% { transform: translate(-3px, 1px) rotate(0deg); }
  87% { transform: translate(3px, 1px) rotate(-1deg); }
  90% { transform: translate(-1px, -1px) rotate(1deg); }
  93% { transform: translate(1px, 2px) rotate(0deg); }
  96% { transform: translate(1px, -2px) rotate(-1deg); }
  100% { transform: translate(2px, 2px) rotate(0deg); }
}


@keyframes expand {
  from { width: 0; height: 0; margin-left: 0; margin-top: 0; opacity: 0.7; }
  to { width: 200vw; height: 200vh; margin-left: -100vw; margin-top: -100vh; opacity: 1; }
}

/* @keyframes slidein {
  from { transform: translateY(-50%); }
  to { transform: translateY(50%); }
} */
/* @keyframes slidein {
  from { top: -100%; }
  to { top: 0%; }
} */
@keyframes slidein {
  from { transform: translateY(-100%); }
  to { transform: translateY(0%); }
}

body, html {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: #fff4d6;
}
